.contact-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 5px 40px;
}

/* THIS IS THE FORM BOX */
.contact-box {
  max-width: 800px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #fff;
  box-shadow: 0px 0px 19px 5px rgba(0, 0, 0, 0.19);
}

/* THIS IS FOR THE DIV ON THE LEFT (IMG) */
.left {
  height: 100%;
  background: url("/public/images/concert.webp") no-repeat center;
  background-size: cover;
}

/* FOR DIV ON RIGHT */
.right {
  padding: 25px 40px;
}

h2 {
  position: relative;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

h2:after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  height: 4px;
  width: 50px;
  border-radius: 2px;
  background-color: #2ec771;
}

.field {
  width: 100%;
  padding: 0.5rem 1rem;
  outline: none;
  border: 2px solid rgba(0, 0, 0, 0);
  background-color: rgba(230, 230, 230, 0.6);
  font-size: 1.1rem;
  margin-bottom: 22px;
}

.field:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.field:focus {
  background-color: #fefefe;
  border: 2px solid rgba(30, 85, 250, 0.47);
}

.area {
  min-height: 100px;
}

.btn {
  width: 100%;
  padding: 0.5rem 1rem;
  font-size: 1.1rem;
  background-color: #000000;
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  transition: 0.3s;
}

.btn:hover {
  background-color: #444444;
}

/* THIS IS TO MAKE FORM MOBILE FRIENDLY WHICH DOES WORK! */
@media screen and (max-width: 400px) {
  .contact-box {
    grid-template-columns: 1fr;
  }

  .left {
    height: 200px;
  }

  /*   .contact-container{
    padding-left:10px;
    padding-right:10px;
   
  } */
}

@media screen and (min-width: 1500px) {
  .contact-box {
    grid-template-columns: 1fr;
  }

  .left {
    height: 200px;
  }

  .contact-container {
    position: relative;
    padding-top: 30vh;
    padding-bottom: 50vh;
  }
}
